import { derived, get, writable } from 'svelte/store';
import { initialCalendarRect } from './constants';
import { ImageFitOption } from './image-fit-option';
import { saveImageToIndexedDB, saveYearToLocalStorage } from './local-workspace-saver';
export const currentWorkspaceItem = writable(undefined);
export const backgroundImageLoaded = writable();
export const currentSelectedImageStore = writable(undefined);
export const canvasWidth = writable(0);
export const canvasHeight = writable(0);
function cloneDefaultMonthData() {
    return {
        backgroundImage: { value: null, isDirty: false },
        backgroundRect: { value: { x: 0, y: 0, width: 0, height: 0, type: 'background' }, isDirty: false },
        calendarRect: { value: Object.assign({}, initialCalendarRect), isDirty: false },
        calendarColor: { value: "#ffffff", isDirty: false },
        boxSize: { value: { width: 100, height: 100 }, isDirty: false },
        imageFitOption: { value: ImageFitOption.Fill, isDirty: false },
    };
}
export const months = writable(Array(12).fill(null).map(() => cloneDefaultMonthData()));
export const selectedMonth = writable(new Date().getMonth());
export const selectedYear = writable(new Date().getFullYear());
export const currentMonthItem = derived([months, selectedMonth], ([$months, $selectedMonth]) => {
    return $months[$selectedMonth];
});
export function updateMonthProperty(property, value) {
    const currentMonthIndex = get(selectedMonth);
    months.update(months => {
        const currentMonth = months[currentMonthIndex];
        const year = get(selectedYear);
        currentMonth[property].value = value;
        currentMonth[property].isDirty = true;
        trySetBackgroundImage(property, value, year, currentMonthIndex);
        for (let i = 0; i < months.length; i++) {
            const month = months[i];
            if (month[property].isDirty) {
                continue;
            }
            month[property].value = value;
            trySetBackgroundImage(property, value, year, i);
        }
        const workspaceItem = get(currentWorkspaceItem);
        if (workspaceItem) {
            workspaceItem.months = months;
        }
        saveYearToLocalStorage(year.toString(), workspaceItem);
        return months;
    });
}
function trySetBackgroundImage(property, value, year, month) {
    if (property !== 'backgroundImage') {
        return;
    }
    saveImageToIndexedDB(year, month, value);
}
